*, :before, :after {
  box-sizing: inherit;
}

html {
  box-sizing: border-box;
}

body {
  margin: 0;
  font-family: Roboto, sans-serif;
  font-size: 16px;
}

img {
  max-width: 100%;
  max-height: 100%;
  display: block;
}

.container {
  max-width: 1094px;
  margin: 0 auto;
  padding: 0 1em;
  display: block;
}

.link {
  white-space: nowrap;
  color: inherit;
  text-decoration: none;
  transition-property: color;
  transition-duration: .2s;
  transition-timing-function: ease;
}

.link:hover {
  color: #90ee90;
}

.btn {
  font-size: inherit;
  color: inherit;
  background-color: inherit;
  border: none;
  outline: none;
  padding: 1em 1.625em;
  transition-property: color, background-color;
  transition-duration: .2s;
  transition-timing-function: ease;
  display: inline-block;
}

.btn_green {
  background-color: #42ae60;
  border-radius: 6px;
}

.btn_green:hover {
  color: inherit;
  background-color: #87bd1d;
}

.header {
  color: #fff;
  background-color: #273720de;
  padding: 2.5em 0;
}

.header__wrapper {
  grid-template-columns: 1fr 1fr 1fr;
  grid-template-areas: ". header__nav header__btns";
  align-items: center;
  display: grid;
}

.header__nav {
  grid-area: header__nav;
  gap: 3em;
  display: flex;
}

.header__btns {
  grid-area: header__btns;
  justify-content: flex-end;
  margin-left: 4em;
  display: flex;
}

.intro {
  color: #fff;
  background-color: #273720;
  padding: 60px 0;
  display: flow-root;
  position: relative;
  overflow: hidden;
}

.intro__wrapper {
  grid-template-columns: 300px auto;
  display: grid;
}

.intro__right {
  position: relative;
  right: -20vw;
}

.intro__right-img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  border-radius: 10px;
}

.intro__title {
  margin: 0;
  font-size: 4.5em;
}

@media (max-width: 720px) {
  .header__wrapper {
    grid-template-columns: auto;
    grid-template-areas: "header__nav"
                         "header__btns";
    gap: 2em;
  }

  .header__btns {
    margin-left: 0;
  }

  .header__nav, .header__btns {
    justify-content: center;
  }
}

